.nav-title {
  font-family: "Roboto Slab", serif;
  color: rgb(209, 138, 123);
  padding-left: 30px;
  padding-right: 5px;
  width: 100%;
  font-size: 3rem;
  height: 100%;
}
.navbar {
  border-bottom: solid 4px rgba(241, 226, 223, 0.9) !important;
}

.navbar-brand,
.navbar-tabs {
  float: left !important;
  margin-right: 0px !important;
}
/* //-----------------NAVLINKS-------------------// */
.link {
  padding: 15px 2px !important;
  margin: 20px 10px;
  height: 30px;
  border: 3px solid transparent;
}
.navbar-burger {
  display: block transparent;
  margin-right: 5px;
  margin-left: auto;
  margin-top: 20px;
}
.btn {
  padding: 20px 15px !important;
  margin: 20px 10px;
  height: 30px;
  border-radius: 8px !important;
}

.signUp {
  background-color: rgb(209, 138, 123) !important;
  color: white;
  font-weight: bolder;
  margin-right: 0;
}
.logOut {
  background-color: rgb(209, 138, 123) !important;
  color: white;
  font-weight: bolder;
  margin-right: 20px;
}
.logIn {
  background-color: rgba(241, 226, 223, 0.6) !important;
  font-weight: bolder;
  margin-left: 0;
}

/* //-----------------NAVLINKS ON HOVER------------------// */
.link.is-active,
.link:focus,
.link:focus-within,
.link:hover,
a.link.is-active,
a.link:focus,
a.link:focus-within,
a.link:hover {
  border-bottom: solid rgb(209, 138, 123) !important;
  color: black;
  background-color: transparent;
  transition: 0.5s;
}

.signUp:hover {
  background-color: rgb(196, 118, 103) !important;
  color: white !important;
}

.logIn:hover {
  background-color: rgba(241, 226, 223, 0.9) !important;
  color: black !important;
}
.logOut:hover {
  background-color: rgb(196, 118, 103) !important;
  color: white !important;
}
.navbar-main:hover {
  transition: all 0.2s ease-in-out;
  transform: scale(1.05);
}
/* //-----------------NAVLINKS ON ACTIVE-------------------// */
.link.is-active,
.link:focus,
.link:focus-within,
.link:active,
a.link.is-active,
a.link:focus,
a.link:focus-within,
a.link:active {
  border: solid rgb(209, 138, 123) !important;
  color: black;
  background-color: transparent;
}

/* //-----------------LOG IN/SIGN UP-------------------// */

@media screen and (max-width: 1023px) {
  .nav-title {
    transform: scale(0.75);
    padding-left: 0px;
  }
  .navbar-burger {
    margin-right: 5px;
    margin-left: auto;
    margin-top: 10px;
  }
  .navbar {
    display: flex;
    flex-wrap: wrap;
  }
  .navbar-menu {
    margin-left: auto;
    min-width: 100%;
    position: absolute;
    z-index: 999;
    top: 60px;
  }
  a.link {
    border-color: rgb(196, 118, 103);
    padding-bottom: 35px !important;
    text-align: center;
    display: block;
  }
  .is-pulled-left {
    float: none !important;
  }
  a.link:active {
    background-color: rgba(241, 226, 223, 0.6) !important;
  }
  a.link:hover {
    background-color: rgba(241, 226, 223, 0.6) !important;
  }

  .btn {
    border: solid 3px !important;
    border-color: rgb(196, 118, 103) !important;
    padding-bottom: 35px !important;
    padding-top: 10px !important;
    text-align: center;
    display: block;
    margin: 20px 10px;
    border-radius: 0px !important;
    background-color: white !important;
    color: rgb(39, 39, 39);
    font-weight: normal;
  }
  a.btn:hover {
    background-color: rgba(241, 226, 223, 0.6) !important;
    color: black !important;
  }
  a.btn:active {
    background-color: rgba(241, 226, 223, 0.6) !important;
    color: black;
  }
}
@media screen and (max-width: 620px) {
  .nav-title {
    font-size: 2.4rem;
    transform: scale(0.75);
    padding-left: 0px;
  }
  .navbar-burger {
    margin-right: 5px;
    margin-left: auto;
    margin-top: 5px;
  }
}
